import React, {Component} from 'react';
import './Carta.css';
import FlipCard from 'react-flipcard';


var ciego = false;
exports.ciego = ciego;

function handleEnter(e){
  var keycode = (e.keyCode ? e.keyCode : e.which);
  if (keycode == '13'){
    ciego = true;
    exports.ciego = ciego;
    document.activeElement.click();
  }
}

document.addEventListener('keydown', handleEnter);

export default class Carta extends Component {
  render() {
    const fueAdivinada = this.props.fueAdivinada;
    return (
      <div className="carta" onClick={this.props.seleccionarCarta}>
        <FlipCard
          flipped={this.props.estaSiendoComparada || this.props.fueAdivinada}
          disabled={true}
        >
          <div className="portada" aria-label={`Figura ${this.props.fila} ${this.props.columna}`}></div>
          <div className="contenido">
            <img src={`/img/${this.props.icono}.png`} role="presentation" aria-label={`Figura ${this.props.fila} ${this.props.columna}: ${this.props.etiqueta}`} width="125" height="125"></img>
          </div>
        </FlipCard>
      </div>
    )
  }  
};


