import shuffle from 'lodash.shuffle';

const NUMERO_DE_CARTAS = 20;

export default () =>  {

  const figuras = ["arma", "asegura", "coloca", "cuida", "establece", "identifica", "informate", "no_propagues", "no_tires", "recoge", "resguarda", "revisa"]

  const mensajes = ["<b>Prepará</b> un equipo de seguridad que puedas tener a disposición en caso de emergencia. Los elementos básicos que debe contener son: <div style='text-align: left;'><ul><li>Agua potable embotellada.</li> <li>Alimentos envasados o enlatados.</li><li>Linterna con pilas (repuesto de pilas).</li><li>Radio a pilas (repuesto de pilas). </li><li>Documentos y dinero (preferentemente guardarlos en bolsa impermeable). </li><li>Muda seca de ropa interior, vestimenta básica y algún abrigo (preferentemente guardarla en bolsa impermeable). </li><li>Medicamentos. </li><li>Un botiquín de primeros auxilios. </li></div>",
                    "<b>Asegurá</b> que todas/os sepan cómo <b>cortar</b> los servicios de agua, luz y gas. ¡Será una de las primeras cosas que deberán hacer en caso de emergencia!",
                    "<b>Colocá</b> todos los productos peligrosos o tóxicos (productos de limpieza, combustibles, ácidos, venenos, desinfectantes, etc.) bien cerrados a una altura prudente donde no los alcance el agua en una eventual inundación, así evitamos el riesgo de contaminación. Cuidá que no se viertan combustibles, ácidos y solventes, ya que pueden producir daños en la salud, reacciones peligrosas e incluso explosivas.",
                    "<b>Debemos</b> tomar el hábito de mantener limpios todos los espacios que frecuentamos, es decir: no tirar basura al piso, a las zanjas o arroyos, sino hacerlo en los lugares permitidos; evitar la acumulación de escombros, residuos, etc. En caso de lluvias esto es fundamental para que el agua pueda drenar y tomar su curso natural.",
                    "<b>Establecé</b> un lugar seguro en la casa o acordá con una vecina/o cercano para acudir en caso de que sea necesario. <b>¡Importante!</b> Si bien debe ser evaluado como la última opción, es decir en caso de no tener la posibilidad de acudir a otro lugar seguro, es bueno saber dónde se encuentra el <b>Punto de Encuentro</b> más próximo, estos están disponibles para recibir y dar seguridad a las personas damnificadas.",
                    "<b>Identificá</b> qué muebles o ropa pueden verse afectados por el agua. Planificá cómo podrías elevarlos en caso de precisarlo y de ser posible, adelantate y colocalos en un lugar alto, ¡te ahorrará mucha energía si hay una emergencia!",
                    "<b>Mantenerse actualizado</b> en cuanto al <b>Plan General de Gestión de Riesgo Municipal</b> es muy importante, identificando dónde se encuentran los Centros de Salud, Puntos de Encuentro, Delegaciones Municipales, Destacamentos de Bomberos, Comisarías y demás información necesaria para saber cómo se gestiona la emergencia en tu barrio y los centros de evacuación. Si no los conocés, podés consultar en el anexo de mapas de este manual o ingresar al portal web del Plan RRI en https://quehacerlaplata.org/",
                    "<b>No propagues rumores</b> o informaciones exageradas sobre la situación. Si te llega algún mensaje escrito o en audio por cualquier red social o servicio de mensajería, revisá las fuentes oficiales para corroborar la información. Debemos evitar el miedo y el caos que podrían provocar los falsos rumores.",
                    "<b>No saques</b> los residuos durante las tormentas, ya que estos podrían obstruir el paso del agua. Es importante saber cuándo sacarlos <b>¡este pequeño hábito repercute en la salud de la ciudad!</b>",
                    "<b>Recogé</b> la basura, escombros, ramas y hojas de tu casa para evitar que obstaculicen el paso del agua durante lluvias intensas y desborde de arroyos.",
                    "<b>Colocá</b> en una bolsa hermética y en un lugar seguro los documentos, partidas de nacimiento, libretas sanitarias, pasaportes, pólizas de seguros, escrituras y cualquier otro documento de valor. Todas las personas que viven en la casa deben conocer la ubicación de estos documentos para ponerlos a salvo.",
                    "<b>Revisá</b> periódicamente el estado de techos, canaletas, rejillas y desagües pluviales de tu casa. Sellá posibles filtraciones con algún material impermeable. <b>¡Importante! Tené especial cuidado al eliminar cualquier tipo de elemento cortante que se acumule en techos, canaletas y desagües ya que puede ser arrastrado con el agua y lastimar a alguien.</b>"
                    ];

                    
  const etiquetas = ["Mochila y equipo de seguridad.",
                     "Cerrar perillas.",
                     "Material toxico a resguardar en zonas altas.",
                     "Bolsa de basura. Símbolo reciclaje.",
                     "Punto de encuentro.",
                     "Muebles y ropa a resguardar en zonas altas.",
                     "Smartphones. Google play.",
                     "Televisor, megáfono y redes sociales.",
                     "Basura, restos de manzana, hueso, cáscara de huevo.",
                     "Bolsa de basura, ramas, escombros, manos.",
                     "Documentos importantes dentro de Ziplok.",
                     "Botella, recipiente, vaso roto, símbolo peligro."
                    ];

  let cartas = [];

  while (cartas.length < NUMERO_DE_CARTAS) {
    const index = Math.floor(Math.random() * figuras.length);
    const carta = {
      fila: 0,
      columna: 0,
      icono: figuras.splice(index, 1)[0],
      mensaje: mensajes.splice(index, 1)[0],
      etiqueta: etiquetas.splice(index, 1)[0],
      fueAdivinada: false
    };

    cartas.push(carta);
    cartas.push({...carta});
  }

  cartas = shuffle(cartas);


  for (var fila = 1; fila <= 4; fila++) {
    for (var columna = 1; columna <= 5; columna++) {
      var index = (fila - 1) * 5 + (columna - 1)
      cartas[index].fila = fila
      cartas[index].columna = columna
    }
 }

  return cartas;

  
};



