import React, { Component } from 'react';
import './Header.css';

export default class Header extends Component {
  render() {
    return (
      <header>
        <div className="encabezado">
          <div className="titulo">Inundaciones - Juego de la memoria
        
              <button className="boton-pc" onClick={this.props.resetearPartida}>
              Volver a jugar
              </button>

              <button className="boton-pc" onClick={this.props.mostrarAyuda}>
              Ayuda
              </button>

              <button className="boton-pc" onClick={this.props.resetearPartida}>
              Salir
              </button>

          </div>
          <div className="titulo">
            Intentos: {this.props.numeroDeIntentos}
          </div>
        </div>

        <div className="div-mobile">
          <button className="boton-mobile" onClick={this.props.resetearPartida}>
          Volver a jugar
          </button>

          <button className="boton-mobile" onClick={this.props.mostrarAyuda}>
          Ayuda
          </button>

          <button className="boton-mobile" onClick={this.props.resetearPartida}>
          Salir
          </button>
        </div>
        

        <div className="descripcion">Descubrí las cartas iguales que te enseñarán sobre recomendaciones a tener en cuenta en caso de inundación.</div>
      </header>
    );
  }
};



