import React, { Component } from 'react';
import Header from './Header';
import Tablero from './Tablero';
import './App.css';
import construirBaraja from './utils/construirBaraja';


const Swal = require('sweetalert2');


const getEstadoInicial = () => {
  const baraja = construirBaraja();
  return {
    baraja,
    parejaSeleccionada: [],
    estaComparando: false,
    numeroDeIntentos: 0    
  };
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = getEstadoInicial();
  }

  render() {
    return (
      <div className="App">
        <Header 
          numeroDeIntentos={this.state.numeroDeIntentos}
          resetearPartida={() => this.resetearPartida()}
          mostrarAyuda={() => this.mostrarAyuda()}
        />
        <Tablero 
          baraja={this.state.baraja}
          parejaSeleccionada={this.state.parejaSeleccionada}
          seleccionarCarta={(carta) => this.seleccionarCarta(carta)}
        />
      </div>
    );
  }

  seleccionarCarta(carta) {
    if (
      this.state.estaComparando ||
      this.state.parejaSeleccionada.indexOf(carta) > -1 ||
      carta.fueAdivinida
    ) {
      return;
    }

    const parejaSeleccionada = [...this.state.parejaSeleccionada, carta];
    this.setState({
      parejaSeleccionada
    });

    if (parejaSeleccionada.length === 2) {
      this.compararPareja(parejaSeleccionada);
    }
  }

  compararPareja(parejaSeleccionada) {
    // Para saber si es ciego y subir el tiempo que se espera luego de voltear las 2 cartas
    var carta = require('./Carta');
    var ciego = carta.ciego;
    
    this.setState({estaComparando: true});



    if(ciego) {
      //Si es ciego se setea 6000ms de timeout
      setTimeout(() => {
        const [primeraCarta, segundaCarta] = parejaSeleccionada;
        let baraja = this.state.baraja;

        //this.verificarSiHayGanador(baraja);

        if (primeraCarta.icono === segundaCarta.icono) {
          //alert(primeraCarta.mensaje);
          let url = '/img/' + primeraCarta.icono + '.png';
          Swal.fire({
            html: primeraCarta.mensaje,
            imageUrl: url,
            confirmButtonText: 'Entendido'
          })
          
          baraja = baraja.map((carta) => {
            if (carta.icono !== primeraCarta.icono) {
              return carta;
            }

            return {...carta, fueAdivinada: true};
          });
        } else {
          
        }

        this.verificarSiHayGanador(baraja);

        
        this.setState({
          parejaSeleccionada: [],
          baraja,
          estaComparando: false,
          numeroDeIntentos: this.state.numeroDeIntentos + 1
        })
      }, 6000)


    } else {

      //Si no es ciego se setea 1000ms de timeout
      setTimeout(() => {
        const [primeraCarta, segundaCarta] = parejaSeleccionada;
        let baraja = this.state.baraja;

        //this.verificarSiHayGanador(baraja);

        if (primeraCarta.icono === segundaCarta.icono) {
          //alert(primeraCarta.mensaje);
          let url = '/img/' + primeraCarta.icono + '.png';
          Swal.fire({
            html: primeraCarta.mensaje,
            imageUrl: url,
            confirmButtonText: 'Entendido'
          })
          
          baraja = baraja.map((carta) => {
            if (carta.icono !== primeraCarta.icono) {
              return carta;
            }

            return {...carta, fueAdivinada: true};
          });
        } else {
          
        }

        this.verificarSiHayGanador(baraja);

        
        this.setState({
          parejaSeleccionada: [],
          baraja,
          estaComparando: false,
          numeroDeIntentos: this.state.numeroDeIntentos + 1
        })
      }, 1000)
    }
  }

  verificarSiHayGanador(baraja) {
    if (
      baraja.filter((carta) => !carta.fueAdivinada).length === 0
    ) {
      //alert(`Ganaste en ${this.state.numeroDeIntentos} intentos!`);
      Swal.fire({
        title: `¡¡¡Felicidades!!!\n ¡Ganaste en ${this.state.numeroDeIntentos} intentos!`,
        imageUrl: '/img/ganaste.png',
      })
    }
  }

  resetearPartida() {
    this.setState(
      getEstadoInicial()
    );
  }

  mostrarAyuda() {
    Swal.fire({
      html: 'Este juego de navegador es una réplica del famoso juego de mesa de la memoria. Consiste en un tablero de 4 filas y 5 columnas, con un total de 20 cartas. Cada carta tiene una figura del lado opuesto, y existen 10 figuras distintas duplicadas en el juego. El jugador en cada turno voltéa dos cartas y si ambas figuras coinciden, quedan boca arriba. En caso contrario ambas cartas se vuelven a voltear. El objetivo del juego es descubrir las 10 parejas distintas, quedando todas las cartas boca arriba en la menor cantidad de intentos posibles.<br><br>La temática del juego está relacionada a las inundaciones, y las figuras del tablero representan acciones a tomar en caso de que se produzcan inundaciones. Cada vez que el jugador descubre una pareja de figuras, se muestra un mensaje que describe la figura y enseña al jugador que recaudos debe tomar en caso de que se produzcan inundaciones.<br><br>Para reiniciar el juego, contamos con el botón "Volver a jugar".',
      imageUrl: 'img/ayuda.png',
      confirmButtonText: 'Entendido'
    })
  }
}

export default App;


